body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{box-sizing:border-box}html{font-family:OpenSans, sans-serif;font-size:16px;scroll-behavior:smooth}body{margin:0;min-height:100vh}img{max-width:100%}h1,h2,h3,h4,h5,h6,p{margin:0;font-weight:normal;padding:0}a{text-decoration:none;color:inherit}button{font-family:inherit;cursor:pointer;display:flex;flex-direction:row;justify-content:center;align-items:center;width:auto;height:48px;padding:10px 15px;text-align:center;letter-spacing:normal;transition:all 0.2s ease-in-out 0s;font-size:14px;font-weight:600;cursor:pointer;color:#fff;background-color:#0c6cf2;border-radius:8px;border-style:solid;border-width:1px;border-color:#0c6cf2}button:hover{border-color:#0a56c1;background-color:#0a56c1}button *{pointer-events:none}input{font-family:inherit;text-align:inherit}::-webkit-scrollbar{width:8px;background-color:rgba(0,0,0,0);-webkit-border-radius:100px}::-webkit-scrollbar:hover{background-color:rgba(0,0,0,0.09)}::-webkit-scrollbar-thumb:vertical{background:rgba(0,0,0,0.5);-webkit-border-radius:100px}::-webkit-scrollbar-thumb:vertical:active{-webkit-border-radius:100px}.container{max-width:1400px;padding:0 10px}.sm-font{font-size:.875rem}.md-font{font-size:1.25rem}.lg-font{font-size:1.875rem}.xl-font{font-size:2.5rem}.clean-list{margin:0;padding:0;list-style-type:none}.clean-list>li{margin:0;padding:0}.flex{display:flex}.flex.column{flex-direction:column}.flex.justify-center{justify-content:center}.flex.align-center{align-items:center}.flex.wrap{flex-wrap:wrap}.flex.space-between{justify-content:space-between}.flex.space-around{justify-content:space-around}.flex.auto-center{justify-content:center;align-items:center}.flex>*.full-grow{flex:1 1}.text-center{text-align:center}.text-right{text-align:right}.text-left{text-align:left}.center-block{display:block;margin:0 auto}.square-ratio{position:relative;overflow:hidden}.square-ratio:before{display:block;content:"";width:100%;padding-top:100%;pointer-events:none}.square-ratio>img,.square-ratio>iframe{position:absolute;top:0;left:0;right:0;bottom:0;height:100%;width:100%}.square-ratio>img{object-fit:cover}.wide-ratio{position:relative;overflow:hidden}.wide-ratio:before{display:block;content:"";width:100%;padding-top:56.25%;pointer-events:none}.wide-ratio>img,.wide-ratio>iframe{position:absolute;top:0;left:0;right:0;bottom:0;height:100%;width:100%}.wide-ratio>img{object-fit:cover}.square-3{position:relative;overflow:hidden}.square-3:before{display:block;content:"";width:100%;padding-top:75%;pointer-events:none}.square-3>img,.square-3>iframe{position:absolute;top:0;left:0;right:0;bottom:0;height:100%;width:100%}.square-3>img{object-fit:cover}.simple-button{background:transparent;color:black;font-size:inherit;font-family:inherit;border:0;opacity:0.6;transition:0.3s all;padding:0;cursor:pointer}.simple-button:disabled{cursor:auto;cursor:initial;opacity:0.2}.simple-button i{display:inline-block;width:100%;height:100%}.simple-button:hover:not(:disabled){opacity:1}.medium-button{font-size:1.5rem}.fast-trans{transition:0.25s all}.normal-trans{transition:0.4s all}.slow-trans{transition:0.7s all}.p5{padding:5px}.p10{padding:10px}.p20{padding:20px}.m5{margin:5px}.m10{margin:10px}.m20{margin:20px}.normBackground{background-repeat:no-repeat;background-position:center;background-size:cover}.bottom-divider{padding-bottom:15px;margin-bottom:15px;border-bottom:1px solid lightgray}.blurred{-webkit-filter:blur(4px);filter:blur(4px)}.simple-form{display:flex;flex-direction:column;width:300px;margin:15px auto}.simple-form>*:not(:last-child){margin-bottom:10px}.simple-form input:not([type="checkbox"]),.simple-form button{padding:5px 8px;width:100%}.simple-form .form-errors{text-align:center;margin-top:24px;color:red;font-size:15px}.container{max-width:1400px;margin:0 auto;padding-left:10px;padding-right:10px}@media (max-width: 600px){.container{max-width:none;max-width:initial}}@media (max-width: 768px){.container{max-width:600px}}@media (max-width: 1400px){.container{max-width:768px}}@media (max-width: 1080px){.container{max-width:1400px}}.gradient{position:absolute;display:flex;height:100%;width:100%;padding:100px;z-index:-1;background-image:url(https://i.ibb.co/JcxdTPc/g34-copy2.png);background-repeat:no-repeat;background-position:center}.simple-cards-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:10px}.page-loading-screen{background:rgba(255,255,255,0.8);position:fixed;width:70px;height:70px;bottom:10vh;left:50%;transform:translateX(-50%);z-index:20;box-shadow:0 0 7px rgba(0,0,0,0.2);border-radius:50%}.loader-wrapper{height:100%;display:flex;justify-content:center;align-items:center;flex-grow:1}.loader-wrapper img{width:80px;height:80px}.notification-box{position:fixed;bottom:15px;right:30px;padding:8px 15px;animation:slide-in-out 2.5s linear;transform:translateY(150%);opacity:1;font-size:.875rem;background-color:rgba(255,255,255,0.85);background-clip:padding-box;border:1px solid rgba(0,0,0,0.1);box-shadow:0 0.25rem 0.75rem rgba(0,0,0,0.1);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);border-radius:.25rem;z-index:5}.notification-box.success{border-color:green;color:green}.notification-box.failure{border-color:#ff7c7c;color:#f53737}.notification-box.warning{border-color:#ffbf48;color:#ff9900}.modal-wrapper{position:fixed;background:rgba(0,0,0,0.5);display:flex;justify-content:center;align-items:center;width:100%;height:100vh;top:0;left:0;z-index:20;animation:fadein 0.25s ease-in-out}.modal-wrapper.fade-out{animation:fadeout 0.25s ease-in-out forwards}.modal-wrapper .modal-content{background:white;max-width:90%;box-shadow:0 0 10px rgba(0,0,0,0.6);position:relative;animation:scalein 0.3s ease-in-out}.modal-wrapper .modal-content>*{max-width:100%}.modal-wrapper .modal-content .dismiss{font-size:32px;width:44px;padding:0;position:absolute;top:5px;right:5px;z-index:15}.modal-wrapper.fade-out .modal-content{animation:scaleout 0.3s ease-in-out forwards}.menus-toggler{position:fixed;width:100%;height:100%;top:0;left:0;z-index:1;background:rgba(0,0,0,0);opacity:0}.confirmation-modal{flex-direction:column;padding:30px;text-align:center}.confirmation-modal button.simple-button{border:1px solid #b0b0b0;margin:0 5px;background:white;box-shadow:0 0 5px rgba(0,0,0,0.5);padding:8px 22px;border-radius:10px}.confirmation-modal button.simple-button.confirm:hover{background:#19a000}.confirmation-modal button.simple-button.cancel:hover{background:#ff3838}.confirmation-modal h4{margin-bottom:30px;font-size:1.7rem}@keyframes slide-in-out{0%{transform:translateY(150%)}12%{transform:translateY(0);opacity:1}90%{transform:translateY(0);opacity:1}100%{transform:translateY(150%);opacity:0}}@keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadeout{from{opacity:1}to{opacity:0}}@keyframes scalein{from{transform:scale(0.6)}to{transform:scale(1)}}@keyframes scaleout{from{transform:scale(1)}to{transform:scale(0.6)}}@keyframes scaleinsoft{from{transform:scale(0.8)}to{transform:scale(1)}}


.navbar-main-container{padding:10px;background-color:#fff;width:100%;position:-webkit-sticky;position:sticky;top:0;z-index:100;box-shadow:rgba(0,0,0,0.08) 0px 1px 12px}.navbar-main-container .navbar-content{display:flex;justify-content:space-between;align-items:center}.navbar-main-container .logo svg{margin-right:5px}

.favorite-teams-container{position:relative}.favorite-teams-container .favorite-teams{visibility:hidden;display:flex;flex-direction:column;position:fixed;bottom:0;right:10px;background-color:#fff;padding:10px;justify-content:flex-start;box-shadow:rgba(0,0,0,0.08) 0px 1px 12px;opacity:0;transition:.5s}.favorite-teams-container .favorite-teams h3{margin-bottom:10px}.favorite-teams-container .favorite-teams img{width:20px;margin-right:10px}.favorite-teams-container .favorite-teams ul{border-bottom:1px solid rgba(128,128,128,0.171);list-style:none;display:flex;justify-content:flex-start;align-items:center;padding:0;padding:5px 0}.favorite-teams-container .show{visibility:visible;opacity:1}.favorite-teams-container .favorite-btn{display:flex;cursor:pointer}.favorite-teams-container .favorite-btn-txt:hover{color:#0c6cf298}.favorite-teams-container .remove-btn{cursor:pointer;padding-left:5px}.favorite-teams-container .remove-btn .anticon{font-size:10px}.favorite-teams-container .remove-btn .anticon:hover{color:#0c6cf298}.favorite-teams-container .favorite-btn-count{position:absolute;right:-12px;font-size:0.6rem;color:white;background-color:#0c6cf298;width:12px;height:12px;border-radius:50%;display:flex;justify-content:center;align-items:center}

.teams-container{display:flex;justify-content:center}.teams-container .loader-container{padding-top:100px;width:400px}

.teamslist-container{width:100%;margin-top:50px;display:flex;flex-direction:column;justify-content:center;align-items:center;overflow:hidden;transition:.1s;opacity:0}.teamslist-container img{width:80px}.teamslist-container .ant-input-group-wrapper{max-width:250px;margin:20px}.teamslist-container .ant-table-content{width:100%}.teamslist-container .ant-table-cell{transition:.5s;opacity:1}@media (max-width: 476px){.teamslist-container .ant-table-cell img{width:20px}}.show{visibility:visible;opacity:1}

.home-container{display:flex;flex-direction:column;justify-content:center;align-items:center}.home-container .text-img{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.home-container h1{width:30ch;font-weight:600}@media (max-width: 576px){.home-container h1{width:inherit;text-align:center}}

